ction {\n  margin-top: 18px;\n  padding: 20px 25px;\n  border-radius: 8px;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  background-color: white;\n}\n\n.list-restaurant > section:hover {\n  cursor: pointer;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.596);\n}\n\n.list-restaurant > section > a {\n  text-decoration: none;\n}\n\n.list-restaurant > section > a > .poster {\n  width: 100%;\n  position: relative;\n  display: inline-block;\n}\n\n.list-restaurant > section > a > .poster > .rating {\n  position: absolute;\n  font-size: small;\n  bottom: 10px;\n  right: 10px;\n  width: 50px;\n  background-color: rgba(0, 0, 0, 0.555);\n  color: white;\n  padding: 3px 5px;\n  border-radius: 5px;\n  z-index: 1;\n}\n\n.list-restaurant > section > a > .poster > img {\n  width: 100%;\n  border-radius: 8px;\n  max-height: 175px;\n  overflow: hidden;\n  object-fit: cover;\n}\n\n.list-restaurant > section > a > h2 {\n  font-size: 14px;\n  text-align: left;\n  color: #537a6b;\n}\n\n.list-restaurant > section > a > h3 {\n  font-weight: lighter;\n  font-size: 14px;\n  text-align: left;\n  color: #537a6b;\n}\n\n.list-restaurant > section > a > p {\n  margin-top: 5px;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-box-orient: vertical;\n  -webkit-line-clamp: 5;\n  text-align: justify;\n  color: black;\n}\n\n.detail-restaurant {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  overflow: hidden;\n  justify-content: center;\n  text-align: justify;\n}\n\n.detail-container {\n  margin: 24px 24px;\n}\n\n.restaurant-name {\n  color: #537a6b;\n  font-size: 20px;\n  text-align: left;\n  margin-bottom: 12px;\n}\n\nh3 {\n  margin-top: 12px;\n  color: #537a6b;\n}\n\n.detail-container > .detail-categories {\n  margin-top: 12px;\n}\n\n.detail-poster {\n  width: 100%;\n  position: relative;\n  display: inline-block;\n}\n\n.detail-rating {\n  position: absolute;\n  font-size: small;\n  bottom: 10px;\n  right: 10px;\n  width: 50px;\n  background-color: